import axios from '@nextcloud/axios';
import { generateUrl, imagePath } from '@nextcloud/router';
import { loadState } from '@nextcloud/initial-state';
import pLimit from 'p-limit';
import { translate as t } from '@nextcloud/l10n';
import logger from '../../logger';
const recommended = {
  calendar: {
    description: t('core', 'Schedule work & meetings, synced with all your devices.'),
    icon: imagePath('core', 'places/calendar.svg')
  },
  contacts: {
    description: t('core', 'Keep your colleagues and friends in one place without leaking their private info.'),
    icon: imagePath('core', 'places/contacts.svg')
  },
  mail: {
    description: t('core', 'Simple email app nicely integrated with Files, Contacts and Calendar.'),
    icon: imagePath('core', 'actions/mail.svg')
  },
  spreed: {
    description: t('core', 'Chatting, video calls, screensharing, online meetings and web conferencing – in your browser and with mobile apps.'),
    icon: imagePath('core', 'apps/spreed.svg')
  },
  richdocuments: {
    description: t('core', 'Collaboratively edit office documents.'),
    icon: imagePath('core', 'apps/richdocuments.svg')
  },
  richdocumentscode: {
    description: t('core', 'Local document editing back-end used by the Collabora Online app.'),
    icon: imagePath('core', 'apps/richdocumentscode.svg')
  }
};
const recommendedIds = Object.keys(recommended);
const defaultPageUrl = loadState('core', 'defaultPageUrl');
export default {
  name: 'RecommendedApps',

  data() {
    return {
      loadingApps: true,
      loadingAppsError: false,
      apps: [],
      defaultPageUrl
    };
  },

  computed: {
    recommendedApps() {
      return this.apps.filter(app => recommendedIds.includes(app.id));
    }

  },

  mounted() {
    return axios.get(generateUrl('settings/apps/list')).then(resp => resp.data).then(data => {
      logger.info("".concat(data.apps.length, " apps fetched"));
      this.apps = data.apps.map(app => Object.assign(app, {
        loading: false,
        installationError: false
      }));
      logger.debug("".concat(this.recommendedApps.length, " recommended apps found"), {
        apps: this.recommendedApps
      });
      this.installApps();
    }).catch(error => {
      logger.error('could not fetch app list', {
        error
      });
      this.loadingAppsError = true;
    }).then(() => {
      this.loadingApps = false;
    });
  },

  methods: {
    installApps() {
      const limit = pLimit(1);
      const installing = this.recommendedApps.filter(app => !app.active && app.isCompatible && app.canInstall).map(app => limit(() => {
        logger.info("installing ".concat(app.id));
        app.loading = true;
        return axios.post(generateUrl('settings/apps/enable'), {
          appIds: [app.id],
          groups: []
        }).catch(error => {
          logger.error("could not install ".concat(app.id), {
            error
          });
          app.installationError = true;
        }).then(() => {
          logger.info("installed ".concat(app.id));
          app.loading = false;
        });
      }));
      logger.debug("installing ".concat(installing.length, " recommended apps"));
      Promise.all(installing).then(() => {
        logger.info('all recommended apps installed, redirecting …');
        window.location = defaultPageUrl;
      }).catch(error => logger.error('could not install recommended apps', {
        error
      }));
    },

    customIcon(appId) {
      if (!(appId in recommended) || !recommended[appId].icon) {
        logger.warn("no app icon for recommended app ".concat(appId));
        return imagePath('core', 'places/default-app-icon.svg');
      }

      return recommended[appId].icon;
    },

    customDescription(appId) {
      if (!(appId in recommended)) {
        logger.warn("no app description for recommended app ".concat(appId));
        return '';
      }

      return recommended[appId].description;
    }

  }
};